function getStringByLanguage(
  languageCode: string,
  stringKey: string,
  strings: any = languageStrings
) {
  return !strings[languageCode] || !strings[languageCode][stringKey]
    ? strings.en[stringKey]
    : strings[languageCode][stringKey];
}

export default {
  getStringByLanguage
};
